import {Component} from 'react';

class CreatePage extends Component {
        state =
                {
                        prompt: '',
                        firstQ: '',
                        secondQ: '',
                        thirdQ: '',
                        fourthQ: '',
                        correctAnswer: '',
                        displayQuestions: false
                };

        addQuestion = (event) => {
                event.preventDefault();

                return(
                        <div></div>
                );
        };

        saveQuestions = (event) => {
                event.preventDefault();
        }
        
        render(){
                return(
                        <div>
                                <h1>Karahoot!</h1>
                                <form action="submit" onSubmit={this.addQuestion}>
                                        <input type="text" onChange={event => this.setState({prompt: event.target.value})}/>
                                                <input type="text" onChange={event => this.setState({firstQ: event.target.value})}/>
                                                <input type="text" onChange={event => this.setState({secondQ: event.target.value})}/>
                                                <input type="text" onChange={event => this.setState({thirdQ: event.target.value})}/>
                                                <input type="text" onChange={event => this.setState({fourthQ: event.target.value})}/>
                                                <input type="text" onChange={event => this.setState({correctAnswer: event.target.value})}/>
                                        <button type="submit">Add Question</button>
                                </form>
                                                <button onClick={this.saveQuestions}>Save QUestions</button>
                        </div>
                );
        };
}

export default CreatePage;
